
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'

import popup from "@/component/popup";
import notify from "@/component/notify";
import avatar from "../imgs/avatar.jpg";
import first from "@/imgs/first.png";
import two from "@/imgs/second.png";
import third from "@/imgs/third.png";
import toast from "@/component/toast";
import { GetUrlParam, convertImgToBase64, beAvatar } from "../js/util.js";

export default {
name: "teamout",
data() {
return {
showError: false, //是否显示错误页面
liveMsg:'',
rankList:[],
msgList:[],
notifyList: [





],
showPopup:false,
showShareGuide:false

};
},
components: {
Flexbox,
FlexboxItem,
toast,
Divider,
Loading,
notify,
Spinner,
popup
},
computed: {
websocket() {
return this.$store.state.websocket;
},
userdata() {
return this.$store.state.userdata;
},
activitydata() {
return this.$store.state.activitydata;
},
},
mounted() {
if (this.userdata.platform == "yst") {
if (window.dd) {
dd.postMessage({
name: "invite",
desc: this.sharedesc
});
dd.onMessage = e => {};
}
}
},
beforeMount() {

if (this.userdata.is_blockade_success != 1) {
this.showError = true;
this.showIndex = 4;
this.errorInfo = "你还未完成必答，暂不能进行PK对决,快去完成必答吧";
}
this.websocket.onmessage = e => {
let re = JSON.parse(e.data);
let res = re.data;
if(re.action == "top"){
this.rankList = res
}
if(re.action == "live"){
this.msgList.push(res)
var container = this.$el.querySelector("#msgbox");
if(this.msgList.length > 20) {
this.msgList.shift();
}
container.scrollTop = container.scrollHeight;
}
if (re.action == "startNotify") {

this.$router.replace({
path: "/beatdown",
query: {
room_id: res.room_id,
activity_id: this.$store.state.activityId,
start_sign: true,
access_token: this.$store.state.access_token
}
});
}
};
},
created(){
this.websocketSend({
channel: "teampk",
action: "top",
data: {
team_pk_id: this.activitydata.team_pk_info.team_pk_id
}
});
},
destroyed() {
clearInterval(this.timer);
},
methods: {
websocketSend(data) {
try {
this.websocket.send(JSON.stringify(data));
} catch (error) {
this.showError = true;
this.showIndex = 4;
this.errorInfo = "网络异常，请稍后退出重试！";
}
},
sendMsg() {
if(this.liveMsg.length > 0){
this.websocketSend({
channel: "teampk",
action: "sendMessage",
data: {
team_pk_id: this.activitydata.team_pk_info.team_pk_id,
message: this.liveMsg
}
});
this.liveMsg = ''
}
},
websocketMess() {
this.websocket.onmessage = e => {
let re = JSON.parse(e.data);
let res = re.data;
if (re.action == "startNotify") {

this.$router.replace({
path: "/beatdown",
query: {
room_id: res.room_id,
activity_id: this.$store.state.activityId,
start_sign: true,
access_token: this.$store.state.access_token
}
});
}
};
}
}
};



