import Vue from 'vue';
import Vuex from 'vuex';
import {
    setCookie,
    getCookie
} from './cookie'
import {
    GetUrlParam
} from "./util.js";
Vue.use(Vuex);
export const store = new Vuex.Store({
    state: {
        userdata: {}, //用户信息
        activitydata: {}, //活动信息
        status: '', //是否绑定
        token: getCookie('access_token'),
        websocket: null,
        wsState: 0,
        activityId: GetUrlParam("activity_id"),
        access_token: GetUrlParam("access_token"),
        avatar_default: '//s1-hn.aghcdn.com/king/avatar.jpg',
        logo_cdn: '//game.aghcdn.com/design/king/avatar_default.png',
        showOffline: false,
        offlineMsg: '',
        share_img: 'data:image/jpg;base64,'
    },
    mutations: {
        setState(state, payload) {
            for (let key in payload) {
                state[key] = payload[key]
            }
        },
        setUserState(state, payload) {
            for (let key in payload) {
                state.userdata[key] = payload[key]
            }
        }
    }
});


