export default {
    name: "login",
    data() {
        return {
            phoneNum: '', //手机号码
            username: '', //姓名
            waitSecond: 0, //等待时间
            tipText: '' //错误信息
        }
    },
    computed: {
        checkText() {
            if (this.waitSecond == 0) {
                return '验证工号'
            } else {
                return '等待... '
            }
        }
    },
    methods: {
        hideLogin() {
            this.$parent.showVertify = false;
        },
        getCode() {
            var pattern = /^[A-Za-z0-9]{3,10}$/;
            if (this.phoneNum == '') {
                this.tipText = '请输入工号';
                return;
            } else if (!pattern.test(this.phoneNum)) {
                this.tipText = '工号格式有误';
                return;
            } else if (this.waitSecond != 0) {
                return;
            } else {
                var timer = null;
                this.waitSecond = 60;
                // timer = setInterval(() => {
                //     if (this.waitSecond > 0) {
                //         this.waitSecond--;
                //     } else {
                //         clearInterval(timer);
                //     }
                // }, 1000)
                this.$ajax
                    .post("/api/user/sendsms?activity_id=" + this.$store.state.activityId, {
                        phone: this.phoneNum
                    })
                    .then(response => {
                        this.waitSecond = 0;
                        let res = response.data;
                        if (res.error != 0) {
                            this.tipText = res.message;
                            this.verifyCode = '';
                            // clearInterval(timer);
                            // this.waitSecond = 0;
                        }
                        if (res.error == 0) {
                            this.verifyCode = res.name;
                            this.tipText = '';
                        }
                    })
                    .catch(error => {});
            }
        },
        preventHide() {
            return;
        },
        submit() {
            if (this.phoneNum == '') {
                this.tipText = '请输入工号';
                return;
            } 
            if (this.username == '') {
                this.tipText = '请输入姓名';
                return;
            }
            this.$ajax
                .post("/api/user/userbind?activity_id=" + this.$store.state.activityId, {
                    phone: this.phoneNum,
                    username: this.username
                })
                .then(response => {
                    if (response.data.error == 0) {
                        this.$parent.showVertify = false;
                        // window.location.reload();
                        window.location.replace("https://game.sgjyun.com?activity_id="+this.$store.state.activityId);
                    }
                    if(response.data.error != 0) {
                        this.tipText = response.data.message;
                    }
                })
                .catch(error => {});
        }
    }
};



