

import Vue from 'vue'
import App from './App'
import router from './router'
import axios from 'axios'
import './css/mixins.less'
import animate from 'animate.css'
import {
store
} from './js/store'
import $ from './js/jquery-3.3.1.min.js'
import AlertPlugin from 'vux/src/plugins/alert/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

Vue.use(AlertPlugin, ToastPlugin)
Vue.use(animate)
Vue.directive('transfer-dom', TransferDom)
Vue.config.productionTip = false
Vue.prototype.$ajax = axios;
/* eslint-disable no-new */

router.beforeEach((to, from, next) => {
console.log('进入' + to.name + '页面');
next();
});
new Vue({
el: '#app',
store,
router,
components: {
App
},
template: '<App/>'
})


