export default {
  name: "activity",
  data() {
    return {};
  },
  computed: {
    activitydata() {
      return this.$store.state.activitydata;
    }
  },
  created() {},
  beforeMount() {
    if (window.dd) {
      dd.postMessage({
        name: "home"
      });
      dd.onMessage = e => {};
    }
  },
  methods: {
    preview() {
      if (window.dd) {
        dd.previewImage({
          current: 0,
          urls: ["http://s1-hn.aghcdn.com/dingtalk/qrcode.png"]
        });
      }
    }
  }
};



