import {
    setCookie,
    getCookie
} from '../js/cookie.js'
export default {
    name: "toast",
    computed: {
        activitydata() {
            return this.$store.state.activitydata;
        }
    },
    props: {
        showIndex: {
            type: Number,
            required: true
        },
        errMsg: {
            type: String,
            required: false
        }
    },
    methods: {
        goPage(page) {
            this.$router.replace({
                path: page,
                query: {
                    updateuser: 1,
                    activity_id: this.$store.state.activityId
                }
            });
        },
        closeMask(index) {
            if (index == 1) {
                if (this.activitydata.regulation_required == 1) {
                    window.localStorage.setItem('isReadReg_' + this.$store.state.activityId, true);
                }
                this.$parent.showLay = false;
            } else if (index == 2) {
                this.$parent.showError = false;
            } else if (index == 3) {
                this.$parent.showError = false;
            }
        }
    }
};



