
import Popup from 'vux/src/components/popup/index.vue'

export default {
name: "notify",
data() {
return {
showRefuse: true,
countdown: 2,
timer: null
}
},
components: {
Popup
},
computed: {
activitydata() {
return this.$store.state.activitydata;
}
},
props: {
refuseUserName: {
type: String
},
msgIndex: {
type: Number
}
},
beforeMount() {
let i = 2;
let counttimer;
this.countdown = String(i);
this.timer = setInterval(() => {
i--;
this.countdown = String(i);
if (i == 0) {
this.$parent.showPopup = false;
this.showRefuse = false;
return;
}
}, 1000);
},
destroyed() {
clearInterval(this.timer);
},
methods: {

}
};



